import React, { useState, useEffect  } from 'react';
import * as Styles from './GameArea.Style.jsx';

import { gameLogic } from '../../lib/gameLogic';
import seedPattern from '../../lib/seedsPattern';

import { StartBar } from '../StartBar';
import { Grid } from '../Grid';

function GameArea({ changeTheme, selectedTheme }) {
	const numOfPatterns = 8;
	const smallGrid = 10;
	const mediumGrid = 20;
	const largeGrid = 40;
	const [isGameActive, setIsGameActive] = useState(false);
	const [gameConfigurations, setGameConfigurations] = useState({ gridSize: smallGrid, seed: 1, speed: 1});
	const [generationCounter, setGenerationCounter] = useState(0);
	const [cellArray, setCellArray] = useState([]);

	const updateGameConfiguration = (key, value) => {
		let newConfiguration = {};
		newConfiguration = Object.assign(newConfiguration, gameConfigurations);
		newConfiguration[key] = value;
		setGameConfigurations(newConfiguration);
	};

	const emptyCellArray = () => {
		const size = gameConfigurations.gridSize * gameConfigurations.gridSize;
		const auxArray = new Array(size).fill(false);
		setCellArray(auxArray);
		setIsGameActive(false);
		setGenerationCounter(0);
	};

	useEffect(() => {
		emptyCellArray();
		// drawPattern(seedPattern[gameConfigurations.seed]);
	}, [gameConfigurations.gridSize]);

	const updateGridSize = (size) => {
		if(!isGameActive) {
			updateGameConfiguration('gridSize', size);
		}
	};

	const updateCellStatus = (index) => {
		const tempArray = JSON.parse(JSON.stringify(cellArray));
		let flag = isGameActive;
		isGameActive && setIsGameActive(false);
		tempArray[index] = !tempArray[index];
		setCellArray(tempArray);
		setTimeout(() => {
			flag && setIsGameActive(true);
		}, 0);
	};

	const startGame = () => {
		setIsGameActive(true);
	};

	const pauseGame = () => {
		setIsGameActive(false);
	};

	const gameStepCicle = () => {
		const actualStateArray = JSON.parse(JSON.stringify(cellArray));
		const newStateArray = gameLogic(actualStateArray, gameConfigurations.gridSize);
		setCellArray(newStateArray);
		setGenerationCounter(generationCounter => generationCounter + 1);
	};

	useEffect(() => {
		let interval = null;
		if (isGameActive) {
			interval = setInterval(() => {
				gameStepCicle();
			}, 500 / gameConfigurations.speed);
		} else if (!isGameActive) {
			clearInterval(interval);
		}
		return () => clearInterval(interval);
	}, [isGameActive, generationCounter]);

	const stepForward = () => {
		gameStepCicle();
	};

	const setSpeed = (value) => {
		updateGameConfiguration('speed', value);
	};

	const drawPattern = (patterArray) => {
		emptyCellArray();
		const gridSize = gameConfigurations.gridSize;

		//const patterArray = seedPattern[gameConfigurations.seed];

		const patternSizeRow = patterArray.length;
		const emptySpaceRow = (gridSize - patternSizeRow) / 2;
		const emptyRow = new Array(gridSize * emptySpaceRow).fill(false);

		const patternSizeCol = patterArray[0].length;
		const emptySpaceCol = (gridSize - patternSizeCol) / 2;
		const emptyCol = new Array(emptySpaceCol).fill(false);
		let newCellArray = [];

		newCellArray = newCellArray.concat(emptyRow);

		patterArray.forEach(element => {
			newCellArray = newCellArray.concat(emptyCol);
			newCellArray = newCellArray.concat(element);
			newCellArray = newCellArray.concat(emptyCol);
		});

		newCellArray = newCellArray.concat(emptyRow);

		setCellArray(newCellArray);
	};

	const setSeed = () => {
		if(!isGameActive) {
			const newSeed = gameConfigurations.seed < numOfPatterns ? gameConfigurations.seed + 1 : 1;
			updateGameConfiguration('seed', newSeed);
			drawPattern(seedPattern[gameConfigurations.seed]);
		}
	};

	return (
		<Styles.Main>
			<Styles.BarContainer>
				<Styles.CounterText> 
                    generation: {generationCounter}
				</Styles.CounterText>
				<StartBar 
					buttonHandlers={{
						start: startGame,
						pause: pauseGame,
						stepF: stepForward,
						reset: emptyCellArray,
						speedValue: gameConfigurations.speed,
						speedHandler: setSpeed,
						setTheme: changeTheme,
						selectedTheme,
					}}
				/>
			</Styles.BarContainer>

			<Grid 
				arrayOfElements={cellArray}
				gameConfigurations={gameConfigurations}
				updateCellStatus={updateCellStatus}
			/>
			
			<Styles.BarContainer>
				<Styles.SeedDiv>
					<Styles.ButtonStyled
						isActive={false}
						width={'130px'}
						onClick={() => setSeed()}
					>
						RANDOM SEED
					</Styles.ButtonStyled>
				</Styles.SeedDiv>
				<div>
					<Styles.ButtonStyled
						isActive={gameConfigurations.gridSize === smallGrid ? true : false}
						onClick={() => updateGridSize(smallGrid)}
					>
						SMALL
					</Styles.ButtonStyled>
					<Styles.ButtonStyled
						isActive={gameConfigurations.gridSize === mediumGrid ? true : false}
						onClick={() => updateGridSize(mediumGrid)}
					>
						MEDIUM
					</Styles.ButtonStyled>
					<Styles.ButtonStyled
						isActive={gameConfigurations.gridSize === largeGrid ? true : false}
						onClick={() => updateGridSize(largeGrid)}
					>
						LARGE
					</Styles.ButtonStyled>
				</div>
			</Styles.BarContainer>
		</Styles.Main>
	);    
}

export default GameArea;